table.readings {
	border-collapse: collapse;
	width: 100%;
}

tr.recto td, tr.verso td, tr.exam td {
	padding-top: .5em;
	padding-bottom: .5em;
	height: 6em;
}

tr.recto td:first-child, tr.verso td:first-child, tr.exam td:first-child {
	width: 15%;
}

tbody + tbody tr th {
	padding-top: 2em;
}

tr.recto {
	background-color: #F8F8F8;
}

tr.verso {
	background-color: #E0E0E8;
}

tr.exam {
	background-color: #E87878;
}
